<template>
  <div>
    <el-menu style="height:80px;"
      :default-active="activeIndex2"
      mode="horizontal"
      @select="handleSelect"
      background-color="#0ec6ce"
      text-color="#FFFFFF"
      active-text-color="#FFFFFF">
      <el-menu-item index="1" style="height:80px;" v-if="common_show">
        <a href="#/" style="text-decoration:none">
          <div class="item">
            <img src="../../../static/images/logo/logo.jpg" class="logo">
          </div>
        </a>
      </el-menu-item>
      <el-menu-item index="2" style="height:80px;">
        <a href="#/" style="text-decoration:none">
          <div class="item">
            首页
          </div>
        </a>
      </el-menu-item>
      <el-menu-item index="3" style="height:80px;" v-if="common_show">
        <a href="#/articleList?type=article" style="text-decoration:none">
          <div class="item">
            推荐作品
          </div>
        </a>
      </el-menu-item>
      <el-menu-item index="4" style="height:80px;" v-if="common_show">
        <a href="#/docList?type=doc" style="text-decoration:none">
          <div class="item">
            技术文档
          </div>
        </a>
      </el-menu-item>
      <el-menu-item index="5" style="height:80px;" v-if="common_show">
        <a href="#/vedioList?type=vedio" style="text-decoration:none">
          <div class="item">
            学习视频
          </div>
        </a>
      </el-menu-item>
      <el-menu-item index="6" style="height:80px;" v-if="common_show">
        <a href="#/projectList?type=project" style="text-decoration:none">
          <div class="item">
            开源项目
          </div>
        </a>
      </el-menu-item>
      <el-menu-item index="7" style="height:80px;" v-if="common_show">
        <a href="#/toolList?type=tool" style="text-decoration:none">
          <div class="item">
            开发工具
          </div>
        </a>
      </el-menu-item>
      <el-menu-item index="8" style="height:80px;">
        <a href="./a" style="text-decoration:none">
          <div class="item">
            学习路线
          </div>
        </a>
      </el-menu-item>
      <el-menu-item index="9" style="height:80px;">
        <a href="./a" style="text-decoration:none">
          <div class="item">
            关于本站
          </div>
        </a>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
export default {
  data () {
    return {
      common_show: true
    }
  },
  created () {
    window.addEventListener('resize', this.handleResize)
  },
  beforeDestroy: function () {
    window.removeEventListener('resize', this.handleResize)
  },
  methods: {
    handleResize (event) {
      this.fullWidth = document.documentElement.clientWidth
      // 判断列数、组件显示
      if (this.fullWidth > 1025) {
        this.common_show = true
      } else {
        this.common_show = false
      }
    }
  }
}
</script>

<style scoped>
.logo {
  width: 80px;
  height: 60px;
}
.item {
  padding-top: 10px;
  height: 70px;
}
.el-menu-item {
  font-size: 1rem;
}
</style>



