<template>
  <div>
    <el-container>
      <el-header style="height:80px;">
        <v-header></v-header>
      </el-header>
      <el-main>
        <el-row :gutter="24">
          <el-col :span="common_span" :offset="common_offset">
            <router-view></router-view>
          </el-col>
          <div v-if="common_show">
            <el-col :span="4">
              <v-right></v-right>
            </el-col>
          </div>
        </el-row>
      </el-main>
      <el-footer>
        <v-foot></v-foot>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import vHeader from './Header.vue'
import vRight from './Right.vue'
import vContent from './Content.vue'
import vFoot from './Foot.vue'
export default {
  data () {
    return {
      common_show: true,
      common_span: 16,
      common_offset: 2
    }
  },
  components: {
    vHeader, vRight, vContent, vFoot
  },
  created () {
    window.addEventListener('resize', this.handleResize)
  },
  beforeDestroy: function () {
    window.removeEventListener('resize', this.handleResize)
  },
  methods: {
    handleResize (event) {
      console.log('111')
      this.fullWidth = document.documentElement.clientWidth
      // 判断列数、组件显示
      if (this.fullWidth > 1500) {
        this.common_span = 16
        this.common_show = true
        this.common_offset = 2
      } else if (this.fullWidth > 1025) {
        this.common_span = 16
        this.common_show = true
        this.common_offset = 1
      } else {
        this.common_span = 24
        this.common_show = false
        this.common_offset = 0
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-header {
  background-color: #0ec6ce;
  padding: 0 15%;
  height: 80px;
}
.el-main {
  background-color: #E9EEF3;
}
.el-footer {
  background-color: #606266;
  height: 100%;
  padding: 0%;
  font-size: 0.8rem;
}
</style>



